.blogPostContainer{
    width: 90%;
}
.blogHeader{
    color: white;
    text-align:center;
}
.postTitle{
    letter-spacing: 0.5px;
    text-transform:capitalize;
    font-size: 30px;
    margin:0;
}
.blogCategory{
    font-size: 14px;
    margin: 10px 0;
    display: block;
    font-weight: 400;
}
.postedBy{
    font-size: 15px;
    font-style:italic;
    display: block;
    padding: 10px 0;
    color: #6c6c6c;
}
.postImageContainer{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}
.postImageContainer img{
    max-width: 100%;
    max-height: 100%;
}
.postContent{
    margin: 10px;
}
.postContent h3{
    font-weight: 400;
    color: #333;
}
.cardHeader span{
    text-transform:uppercase;
    font-size: 15px;
    text-align:center;
    padding: 10px 0;
    box-sizing: border-box;
    letter-spacing: 1px;
    color: #565656;
}
.postContent p{
    font-size: 14px;
    color: #6c6c6c;
    font-weight: 300;
    letter-spacing: 1px;
    margin: 20px 0;
    padding-bottom: 20px;
}